div {
  &#out {
    display: table;
    height: 80%;
    width: 100%;

    h1 {
      font-size: clamp(2.6rem, 5vw, 3.2rem);
      margin-bottom: 0.6rem; } }

  &#middle {
    display: table-cell;
    vertical-align: middle; }

  &#in {
    margin-left: auto;
    margin-right: auto;
    width: 50%;

    ul {
      margin-top: 6px;

      li {
        font-size: 1.15em;
        line-height: 1.4em; } } } }
